#skills {
    padding: 50px 0;
    text-align: center;
  }
  
  .skills-container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
  
  .skill {
    width: 250px;
    margin: 20px;
  }
  
  .skill-name {
    margin-bottom: 10px;
  }
  
  .skill-level {
    height: 20px;
    background-color: #e6e6e6;
    border-radius: 5px;
    overflow: hidden;
  }
  
  .skill-level-bar {
    height: 100%;
    background-color: #4caf50;
  }
  
  .skill-level-value {
    display: block;
    margin-top: 10px;
  }
  